.popular{
    display:flex;
    align-items: center;
    flex-direction: column;
    gap:10px;
    height:90vh;
}
.popular h1{
    color:#171717;
    font-size: 50px;
    font-weight: 500;
}
.popular hr{
    width:200px;
    height:6px;
    border-radius: 10px;
    background:#252525;

}
.popular-item{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap:30px;
}