.loginsignup{
    width:100%;
    height:72vh;
    background: #fce3ce;
    padding-top:100px;
    padding-bottom: 700px;
    
}
.loginsignup-container{
    width:580px;
    height:600px;
    background: white;
    margin:auto;
    padding:40px 60px;
}
.loginsignup-container h1{
    margin:20px 0px;
}
.loginsignup-fields{
   display:flex;
   flex-direction: column;
   gap:29px;
}
.loginsignup-fields input{
    height:72px;
    width:100%;
    padding-left:20px;
    border:1px solid #c9c9c9;
    outline:none;
    color:#5c5c5c;
    font-size: 18px;
    border-radius: 10px;
}
.loginsignup-container button{
    width:580px;
    height:72px;
    color:white;
    background: #FF4141;
    margin-top: 30px;
    border:none;
    font-size: 24px;
    font-weight: 500;
    border-radius: 30px;
}
.loginsignup-login {
    margin-top:20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.loginsignup-login span{
    color:#ff4141;
    font-weight: 600;
}
.loginsignup-agree{
    display:flex;
    align-items: center;
    margin-top: 25px;
    gap:20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
