.cartitems{
    margin:100px 170px;
}
.cartitems hr{
    height:3px;
    background: #e2e2e2;
    border:0;
}
.cartitems-format-main{
    display:grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap:75px;
    padding:10px 0px;
    color:#454545;
    font-size: 15px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;

}
.carticon-product-icon{
    height:62px;
}
.carticon-remove-icon{
    width:15px;
    margin:0px 40px;
    cursor:pointer;
}
.cartitems-quantity{
    width:64px;
    height: 50px;
    border:2px solid #ebebeb;
    background: white;
}
.cartitems-down{
    display:flex;
    margin:100px 0px;
}
.cartitems-total{
    flex:1;
    display:flex;
    flex-direction: column;
    margin-right: 20px;
    gap:40px;
}
.cartitems-total-item{
    display:flex;
    justify-content: space-between;
    padding:14px 0px;
}
.cartitems-total button{
    width:332px;
    height:78px;
    outline:none;
    border: none;
    background: #ff5a5a;
    color:#ffff;
    font-size: 22px;
    cursor:pointer;
    font-weight: 800;
    margin: 40px;
    padding: 20px 20px;
    
    cursor:pointer;
}
.cartitems-promocode{
    flex:1;
    font-size: 16px;
    font-weight: 600;
}
.cartitems-promocode p{
    color:#555;
}
.cartitems-promobox{
    width:504px;
    margin-top: 15px;
    padding-left: 20px;
    height:58px;
    background: #eaeaea;
}
.cartitems-promobox input{
    border:none;
    outline:none;
    background: transparent;
    font-size: 16px;
    width:330px;
    height:40px;


}

.cartitems-promobox button{
    width:170px;
    height:58px;
     font-size: 16px;
     background: black;
     color:white;
     cursor:pointer;


}
